.acme-malarkey {
  text-transform: capitalize;
  color: #cb3837;
}

.acme-malarkey:after {
  animation: cursor-blink 0.4s linear infinite;
  content: "|";
  color: #cb3837;
}

@keyframes cursor-blink {
  1% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

.acme-navbar-text {
  color: white;
}
.navbar-inverse .navbar-nav>li {
    display: inline;
    margin-right: 20px;
    margin-top: 20px;
}
.navbar {
    background: #FFF;
    overflow: hidden;
    border: 0;
    border-radius: 0 !important;
    text-align: left;
    -webkit-box-shadow: 0px 5px 11px 0px rgba(50, 50, 50, 0.08);
    box-shadow: 0px 5px 11px 0px rgba(50, 50, 50, 0.08);
}
#main-nav {
    position: absolute;
    width: 100%;
    z-index: 1000;
    min-height: 75px;
}
#main-nav.fixed {
    position: fixed !important;
    top: 0;
}
.navbar-inverse .navbar-nav>li>a {
    color: #404040;
    padding: 0;
    line-height: 35px;
}
.navbar-inverse .navbar-nav>li>a:hover {
    color: #e96656;
    -webkit-transition: all 700ms;
    transition: all 700ms;
}
.navbar-brand{
  padding-top: 0;
}
